}
.text-black {
  --tw-text-opacity: 1;
  color: rgb(0 0 0 / var(--tw-text-opacity));
}
.text-signUpColor {
  --tw-text-opacity: 1;
  color: rgb(0 149 246 / var(--tw-text-opacity));
}
.text-signUpComments {
  --tw-text-opacity: 1;
